#breadcrumbs
  > span
    @apply font-outfit font-normal text-xs lg:text-sm
    font-feature-settings: 'ss02' on
    span
      a
        @apply text-xs lg:text-sm transition-all duration-200 ease-in-out hover:text-blue-shade-3 inline-block
    span.breadcrumb_last
      @apply block md:inline